  .title-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;

    h5 {
      margin-bottom: 0;
    }

    svg {
      cursor: pointer;
      font-size: 1.25rem;
    }
  }
}

.form-label {
  display: block;
  font-size: $small-text;
  margin-bottom: 0.5rem;
  text-transform: capitalize;
}

.form-input,
.form-textarea,
.form-select {
  width: 100%;
  padding: 0.375rem 0.75rem;
  border-radius: $border-radius;
  background: $background-color;
  border: 1px solid $gray-200;
  outline: none;

  &:active {
    border: 1px solid $gray-200;
  }
}
.form-input,
.form-select,
.btn-block {
  height: 35px;
}
.form-row {
  margin-bottom: 1rem;
}

.form-textarea {
  height: 7rem;
}

