@use "../abstracts/index" as *;

.shared-layout {
  background: $gray-50; ////////////
  .dashboard {
    display: grid;
    grid-template-columns: 1fr;

    .dashboard-page {
      width: 90vw;
      margin: 0 auto;
      padding: 1rem 0;
    }
  }

  @media (min-width: 992px) {
    .dashboard {
      grid-template-columns: auto 1fr;

      .dashboard-page {
        width: 95%;
      }
    }
  }
}
